import React, { useEffect } from "react";
import Header from "components/Header";
import HeaderFV from "components/HeaderFV";
import USPS from "components/USPS";
import About from "components/About";
import Value from "components/Value";
import Details from "components/Details";
import CTA from "components/CTA";
import Cover from "components/Cover";
import Partners from "components/Partners";
import Specials from "components/Specials";
import Roadmap from "components/Roadmap";
import RoadmapStatic from "components/RoadmapStatic";
import FAQ from "components/FAQ";

export default function Home() {
  useEffect(() => {
    document.body.classList.add("scrollbar");
  }, []);

  const aS = {
    transform: "scale(1)",
    opacity: 1,
    transition: { type: "spring", duration: 1, bounce: 0.3, delay: 0 },
  };
  const iA = { transform: "scale(0.5)", opacity: 0 };

  var body = document.body,
    html = document.documentElement;

  var height = Math.max(
    body.scrollHeight,
    body.offsetHeight,
    html.clientHeight,
    html.scrollHeight,
    html.offsetHeight
  );

  const tH = Math.round(0.1 * (height / 2.5)) / 100;

  const dL = "https://discord.gg/MytgAFK2sT";

  return (
    <>
      <Header discordLink={dL} />

      <HeaderFV discordLink={dL} />
      {/*<Cover aS={aS} iA={iA} tH={tH} />*/}
      {/* <USPS />*/}
      <About aS={aS} iA={iA} tH={tH} discordLink={dL} />
      <Value aS={aS} iA={iA} tH={tH} discordLink={dL} />
      <Details aS={aS} iA={iA} tH={tH} />
      <Roadmap />
      <RoadmapStatic aS={aS} iA={iA} tH={tH} />
      <Partners aS={aS} iA={iA} tH={tH} />
      {/*<FAQ />*/}
      <Specials aS={aS} iA={iA} tH={tH} />
      <CTA discordLink={dL} />
    </>
  );
}
