import React, { useEffect, useState } from "react";
import colors from "assets/img/colors.png";
import Confetti from "./../../node_modules/react-confetti";
import {
  claimBoxingBoy,
  loadTokenForBoxingBoy,
  connectToBlockchain,
  isConnectedToBlockchain,
  initWeb3,
  isWeb3Ready,
  loadAllMintedBoxingBoyz,
  tokenIdFromBoxingBoy,
} from "../libraries/Blockchain";
import { Popup } from "libraries/functions";
import CountDown from "components/Countdown";
import ring from "./../assets/img/ring.png";
import Header from "./../assets/img/boxignBackground.png";

export default function HeaderFV({ discordLink }) {
  const [confettiRun, setConfettiRun] = useState(false);
  const [minting, setMinting] = useState(false);
  const [future, setFuture] = useState(Date.parse("December 5, 2021 11:11:11"));
  const [countdownCompleted, setCountdownCompleted] = useState(false);

  function onError(error) {
    if (error.code !== 4001) {
      Popup(
        "Error",
        "An error with MetaMask occured. Please try again later.",
        "red"
      );
    }
    setMinting(false);
  }
  function onDone() {
    document.getElementById("confettiOverlay").style.display = "block";
    Popup(
      "Congratulations!",
      "You succesfully acquired one of the Boxing Boyz!",
      "green"
    );
    setConfettiRun(true);
    setTimeout(function () {
      document.getElementById("confettiOverlay").classList.add("fadeOut");
    }, 7000);
    setTimeout(function () {
      document.getElementById("confettiOverlay").style.display = "none";
      document.getElementById("confettiOverlay").classList.remove("fadeOut");
    }, 9000);
    setMinting(false);
  }

  useEffect(() => {
    //componentDidMount();
  }, []);

  const loadBlockchainData = async () => {
    if (!isWeb3Ready() || !isConnectedToBlockchain()) {
      return;
    }
  };
  const componentDidMount = async () => {
    try {
      let result = await initWeb3();
      if (!result) {
        window.alert(
          "Non-Ethereum browser detected. You should consider trying MetaMask!"
        );
        return;
      }

      await connectToBlockchain();
    } catch (err) {
      alert("Error: Please reload the page");
    }
  };

  function MintButton() {
    return (
      <div
        className="mintButton center"
        onClick={async () => {
          await componentDidMount();
          if (isConnectedToBlockchain()) {
            setMinting(true);
            claimBoxingBoy(1)
              .on("error", async (error, receipt) => {
                console.log("error");
                await onError(error);
              })
              .on("receipt", async () => {
                await onDone();
              });
          } else {
            alert("wallet not connected");
          }
        }}
      >
        <h1 className="disableSelect" style={{ fontSize: "45px" }}>
          {!minting ? (
            "Mint"
          ) : (
            <div
              className=""
              style={{
                width: "100%",
                height: "100%",
                marginLeft: "-38px",
                marginTop: "-92px",
              }}
            >
              <div
                className="loading"
                style={{ width: "100%", height: "100%", transform: "scale(1)" }}
              >
                <div></div>
                <div></div>
              </div>
            </div>
          )}
        </h1>
      </div>
    );
  }

  function onCountdownComplete() {
    setCountdownCompleted(true);
  }

  return (
    <div className="desktopRelative">
      <Confetti
        id="confettiOverlay"
        gravity={0.03}
        run={confettiRun ? true : false}
        opacity={confettiRun ? 1 : 0}
        numberOfPieces={500}
        style={{ zIndex: "100" }}
        initialVelocityY={200}
      />
      <div className="centerImage">
        <img
          src={window.innerWidth > 800 ? Header : ring}
          alt="boxing-ring"
          id="boxingRing"
        />
      </div>
      <div
        className="maxWidthContainer HeaderWImage"
        style={{ marginTop: "-60px" }}
      >
        <div className="column11" style={{ height: "100%" }} id="scrollTop">
          <div
            className={"Vcenter"}
            style={{
              flexDirection: "column",
              padding: "25px",
              position: "relative",
            }}
          >
            <h1 style={{ fontSize: "72px" }}>
              JOIN THE <br />
              <span className="coloredText highlightedText">BOXING BOYZ</span>
              <br /> TODAY
            </h1>
            <h4
              className="removeOnSmallScreen"
              style={{
                marginTop: "70px",
                maxWidth: "450px",
                textShadow: "2px 2px 2px rgba(0, 0, 0, 0.7)",
              }}
            >
              The Boxing Boyz is a 3D animated NFT collection of 10,000 unique
              Boxing characters. This project is created with a vision to
              combine the real boxing world with the digital revolution through
              a play to earn game.
            </h4>
            {/*<CountDown
              future={future}
              onComplete={onCountdownComplete}
              discordLink={discordLink}
            />*/}
            <div className="flexColumns">
              <a
                href={discordLink}
                target="_blank"
                style={{
                  marginTop: "50px",
                  display: "flex",
                  alignItems: "center",
                  justifyContent: "center",
                }}
              >
                <div
                  className="btn roundBtn discordButton disableSelect hideOnSmallScreens"
                  style={{ margin: "unset" }}
                >
                  <svg viewBox="0 0 640 512">
                    <path
                      fill="currentColor"
                      d="M524.531,69.836a1.5,1.5,0,0,0-.764-.7A485.065,485.065,0,0,0,404.081,32.03a1.816,1.816,0,0,0-1.923.91,337.461,337.461,0,0,0-14.9,30.6,447.848,447.848,0,0,0-134.426,0,309.541,309.541,0,0,0-15.135-30.6,1.89,1.89,0,0,0-1.924-.91A483.689,483.689,0,0,0,116.085,69.137a1.712,1.712,0,0,0-.788.676C39.068,183.651,18.186,294.69,28.43,404.354a2.016,2.016,0,0,0,.765,1.375A487.666,487.666,0,0,0,176.02,479.918a1.9,1.9,0,0,0,2.063-.676A348.2,348.2,0,0,0,208.12,430.4a1.86,1.86,0,0,0-1.019-2.588,321.173,321.173,0,0,1-45.868-21.853,1.885,1.885,0,0,1-.185-3.126c3.082-2.309,6.166-4.711,9.109-7.137a1.819,1.819,0,0,1,1.9-.256c96.229,43.917,200.41,43.917,295.5,0a1.812,1.812,0,0,1,1.924.233c2.944,2.426,6.027,4.851,9.132,7.16a1.884,1.884,0,0,1-.162,3.126,301.407,301.407,0,0,1-45.89,21.83,1.875,1.875,0,0,0-1,2.611,391.055,391.055,0,0,0,30.014,48.815,1.864,1.864,0,0,0,2.063.7A486.048,486.048,0,0,0,610.7,405.729a1.882,1.882,0,0,0,.765-1.352C623.729,277.594,590.933,167.465,524.531,69.836ZM222.491,337.58c-28.972,0-52.844-26.587-52.844-59.239S193.056,219.1,222.491,219.1c29.665,0,53.306,26.82,52.843,59.239C275.334,310.993,251.924,337.58,222.491,337.58Zm195.38,0c-28.971,0-52.843-26.587-52.843-59.239S388.437,219.1,417.871,219.1c29.667,0,53.307,26.82,52.844,59.239C470.715,310.993,447.538,337.58,417.871,337.58Z"
                    ></path>
                  </svg>
                  Join Discord
                </div>
              </a>
              <div
                className="container glassContainer center LaunchingBox"
                style={{
                  width: "210px",
                  height: "90px",
                  borderRadius: "500px",
                }}
              >
                <h3>Launching in</h3>
                <h2 style={{ fontSize: "24px" }}>December</h2>
              </div>
            </div>

            <div
              className={window.innerWidth > 800 ? "" : "center"}
              id="launchingBox"
            >
              {/*<div
                className="container glassContainer center"
                style={{
                  width: "200px",
                  height: "100px",
                  borderRadius: "20px",
                }}
              >
                <h3>Launching in</h3>
                <h2 style={{ fontSize: "24px" }}>December</h2>
              </div>*/}
            </div>
          </div>
          <div className="center">
            {!countdownCompleted ? (
              /*<CountDown future={future} onComplete={onCountdownComplete} />*/
              <div style={{ height: "200px" }}></div>
            ) : (
              <MintButton />
            )}
            {/*<img className="colorBackground disableSelect" src={colors}></img>*/}
          </div>
        </div>
      </div>
    </div>
  );
}
