import React, { useEffect, useState } from "react";
import { motion, useAnimation } from "framer-motion";
import { useInView } from "react-intersection-observer";
import "styles/static-roadmap.css";

export default function Details({ aS, iA, tH }) {
  const [ref, inView] = useInView({ threshold: tH });
  const [animated, setAnimated] = useState(false);
  const content = [
    {
      title: "TOKEN STRUCTURE",
      content:
        "By staking your Boxing Boy you will be able to earn $BOXING tokens every day. With these tokens you can participate in many of our online events using your own Boxing Boy and much more. ",
    },
    {
      title: "GAME DEVELOPMENT",
      content:
        "By having a Boxing Boy you will be able to compete in our online events and games. There will be weekly tournaments and a huge monthly championship in which you can participate with tokens. The winners of these tournaments will receive amazing rewards. You will also be able to fight your friends or enemies with 1-vs-1 battles, in a friendly fight or by putting tokens on the line. Last but not least, you and your Boxing Boy will be able to take boxing classes from the biggest champions in the world. We are already working on developing a VR game in the metaverse where you will be able to use your Boxing Boy as an avatar. This game will be a revolutionary utility in the NFT world, more on this later. ",
    },
    {
      title: "HUGE COLLABORATIONS",
      content:
        "We have huge partnerships with boxing organisations, professional boxers and big influencers. These people all support this project completely and are helping us to make sure we create something for the boxing community that is never seen before. With these partnerships we are also able to do crazy giveaways and have amazing prices in our tournaments. You (as a holder) will get the chance to be part of the creative process behind future collaborations and partnerships. ",
    },
    {
      title: "EVOLUTION OF THE RANKS",
      content:
        "Make your voice heard. Being an active member of the community will allow you to work your way up and have a bigger power of decision through votes and surveys.",
    },
  ];
  const animations = [
    useAnimation(),
    useAnimation(),
    useAnimation(),
    useAnimation(),
    useAnimation(),
    useAnimation(),
    useAnimation(),
    useAnimation(),
    useAnimation(),
  ];

  const delayInterval = 0.2;

  useEffect(() => {
    if (!animated) {
      if (inView) {
        for (let i = 0; i < animations.length; i++) {
          aS.transition.delay = delayInterval * i;
          animations[i].start(aS);
        }
        setAnimated(true);
      } else {
        for (let i = 0; i < animations.length; i++) {
          animations[i].start(iA);
        }
      }
    }
  }, [inView]);

  return (
    <div
      className="maxWidthContainer"
      style={{ padding: "50px 25px", position: "relative" }}
      id="scrollDetails"
      ref={ref}
    >
      <h3 className="coloredText">The future</h3>
      <h1 style={{ marginBottom: "40px" }}>DEVELOPMENT ROADMAP</h1>
      <div className="rmsTimeline"></div>
      <div style={{ marginTop: "80px" }}>
        {content.map((indContent, index) => {
          return (
            <motion.div animate={animations[index]} className="rmsTPoint">
              <div className="rmsTPMark">
                <div className="rmsTPMDot"></div>
                <h2 className="rmsTPMNumber">0{index + 1}</h2>
              </div>
              <div className="rmsTContent">
                <h2 style={{ fontSize: "24px", marginBottom: "10px" }}>
                  {indContent.title}
                </h2>
                <p>{indContent.content}</p>
              </div>
            </motion.div>
          );
        })}
      </div>
    </div>
  );
}
