import React, { useEffect, useState } from "react";
import BB1 from "./../assets/img/BB1.png";
import { motion, useAnimation } from "framer-motion";
import { useInView } from "react-intersection-observer";

export default function About({ aS, iA, tH, discordLink }) {
  const [ref, inView] = useInView({ threshold: tH });
  const animation = useAnimation();
  const [animated, setAnimated] = useState(false);

  useEffect(() => {
    if (!animated) {
      if (inView) {
        animation.start(aS);
        setAnimated(true);
      } else {
        animation.start(iA);
      }
    }
  }, [inView]);

  return (
    <div className="maxWidthContainer" ref={ref}>
      <div
        className="column11"
        style={{ padding: "100px 0px" }}
        id="scrollAbout"
      >
        <div className="flexContainer order2">
          <h3 className="coloredText">About Boxing Boyz</h3>
          <h1 style={{ marginTop: "-5px", marginBottom: "20px" }}>OUR STORY</h1>
          <p>
            The boxing boyz are 10.000 boyz that come to visit the earth with
            only one mission, becoming the world champion. To become the world
            champion they need to train and get better every day. The boxing
            boyz will compete once a month in the famous Boxing Boyz
            tournaments. At these tournaments they are able to fulfill their
            mission and become the champion. Will you help your boxing boyz
            become the world champion?
            <br />
            <br />
            This project was created by a team that has deep roots in the boxing
            industry. By working closely with boxers and other people from this
            industry, a project has been created that is made by and for boxers
            and boxing fans.
          </p>
          <a href={discordLink} rel="noreferrer" target="_blank">
            <div
              style={{ width: "150px", marginTop: "40px" }}
              className="btn roundBtn"
            >
              Join Now
            </div>
          </a>
        </div>
        <motion.div
          className="center order1 nftImage"
          animate={animation}
          style={{ padding: "50px" }}
        >
          <img
            className="NFTImageImg"
            style={{ width: "100%", borderRadius: "25px" }}
            src={BB1}
          ></img>
        </motion.div>
      </div>
    </div>
  );
}
